import React,{useEffect,useState} from 'react'; import API from '../api'; import AssignmentCard from '../components/AssignmentCard'; export default function Assignments(){ const [assignments,setAssignments]=useState([]); useEffect(()=>{ API.get('/assignments/class/1').then(r=>setAssignments(r.data)).catch(()=>setAssignments([])); },[]); return (<div className='center-container'><h1>Assignments</h1>{assignments.map(a=><div key={a.id} className='card' style={{marginBottom:8}}><AssignmentCard a={a} /></div>)}</div>) }