import React,{useState} from 'react'; import API from '../api'; export default function SubmissionForm({assignmentId,onSubmitted}){ const [content,setContent]=useState(''); const [file,setFile]=useState(null); const submit=async e=>{ e.preventDefault(); const fd=new FormData(); fd.append('content',content); if(file) fd.append('file',file); await API.post(`/submissions/${assignmentId}/submit`, fd); alert('Submitted'); if(onSubmitted) onSubmitted(); }; return (<form onSubmit={submit}><textarea value={content} onChange={e=>setContent(e.target.value)} className='w-full p-2 border rounded' /><input type='file' onChange={e=>setFile(e.target.files[0])} /><div style={{marginTop:8}}><button type='submit' className='btn btn-primary'>Submit</button></div></form>) }