import React,{useState} from 'react'; import API from '../api'; export default function Register(){ const [name,setName]=useState(''); const [email,setEmail]=useState(''); const [password,setPassword]=useState(''); const [role,setRole]=useState('student'); const submit=async e=>{ e.preventDefault(); await API.post('/auth/register',{name,email,password,role}); window.location.href='/login'; }; return (<div className='center-container'><h2>Register</h2><form onSubmit={submit} className='card'><input value={name} onChange={e=>setName(e.target.value)} placeholder='name' className='w-full p-2 border rounded' /><input value={email} onChange={e=>setEmail(e.target.value)} placeholder='email' className='w-full p-2 border rounded' /><input type='password' value={password} onChange={e=>setPassword(e.target.value)} placeholder='password' className='w-full p-2 border rounded' /><select value={role} onChange={e=>setRole(e.target.value)} className='w-full p-2 border rounded'><option value='student'>Student</option><option value='teacher'>Teacher</option></select><div style={{marginTop:8}}><button className='btn btn-primary' type='submit'>Register</button></div></form></div>) }