import React, {useEffect,useState} from 'react'; import API from '../api'; export default function Grades(){ const [grades,setGrades]=useState([]); useEffect(()=>{ API.get('/grades').then(r=>setGrades(r.data)).catch(()=>setGrades([])); },[]); return (<div className='center-container'><h1>Grades</h1>{grades.map((g,i)=>(<div key={i} className='card' style={{marginBottom:8}}><div>{g.assignment_title} - {g.grade ?? 'Pending'}</div></div>))}</div>) }
